import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue';
import otherLogin from '../../components/home/other-login.vue';
export default {
	components:{
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			status:false, //false 代表账号密码登录 ；true代表手机号登陆
			disabled:true,
			loading:false,
			username:"",
			password:"",
			phone:"",
			checknum:"",
			codetime:0,
		}
	},
	watch:{
		username(val){
			this.OnBtnChange()
		},
		password(val){
			this.OnBtnChange()
		},
		phone(val){
			this.OnBtnChange()
		},
		checknum(val){
			this.OnBtnChange()
		}
	},
	methods: {
		//验证手机号码
		isPhone(){
			let mPattern =  /^1[3456789]\d{9}$/;
			return mPattern.test(this.phone)
			
		},
		//获取验证码
		getCheckNum(){
			//验证手机号码合法性
			  let mPattern =  /^1[3456789]\d{9}$/;
			  if(!this.isPhone()){
				  uni.showToast({
				  	title:"请输入合法的手机号",
					icon:"none"
				  })
				  return;
			  }
			  
			if(this.codetime>0){
				uni.showToast({
					title:"不能重复获取",
					icon:"none"
				});
				return;
				}
				//请求服务器，发送验证
				//发送成功 开启倒计时
				this.codetime=5
				let timer=setInterval(()=>{
					this.codetime--;
					if(this.codetime<1){
						clearInterval(timer)
						this.codetime=0
					}
				},1000)
					
			},
		//初始化表单
		initInput(){
			this.username='',
			this.password='',
			this.phone='',
			this.checknum=''
		},
		//改变按钮状态
		OnBtnChange(){
			if( (this.username && this.password) || (this.phone && this.checknum) ){
				this.disabled = false;return;
			}
			this.disabled=true
		},
		//切换登录状态
		changeStatus(){
			//一切换状态就调用初始化
			this.initInput()
			this.status=!this.status
		},
		//返回上一步
		back(){
			
		},
		//提交登录
		submit(){
			//账号密码登录
			if(!this.status){
				return
			}
			//验证码登录
			//验证手机号合法性
			if(!this.isPhone()){
				  uni.showToast({
					title:"请输入合法的手机号",
					icon:"none"
				  })
				  return;
			}
		}
		
	}
}
